* {
  margin: 0;
  padding: 0;
}

body {
  width: 100vw;
  height: 100vh;
}

.container {
  width: 100vw;
  height: 100vh;
  background-color: #242424;
  background-image: linear-gradient(#bf7bff 1px, #0000 1px), linear-gradient(to right, #bf7bff 1px, #242424 1px);
  background-size: 40px 40px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  animation: fadeInBackground 11s, moveBackground 2.4s 11s infinite alternate;
  display: flex;
}

.container img {
  width: 800px;
  -webkit-filter: drop-shadow(10px 5px 5px #222);
  filter: drop-shadow(10px 5px 5px #222);
  margin-bottom: 50px;
  animation: rubberBand 2s 11s, fadeInTitle 11s;
}

#canvas {
  --glow-color: #d9b0ff;
  --glow-spread-color: #bf7bffc7;
  border: .25em solid var(--glow-color);
  background-image: url("background.b5aa3883.png");
  background-repeat: none;
  box-shadow: 0 0 2em .1em var(--glow-color), 0 0 6em 1em var(--glow-spread-color), inset 0 0 .75em .25em var(--glow-color);
  background-size: cover;
  outline: none;
  animation: zoomInDown 11s;
}

@keyframes fadeInBackground {
  0% {
    filter: brightness(0);
    background-size: 40px 1px;
  }

  100% {
    filter: brightness();
    background-size: 40px 40px;
  }
}

@keyframes moveBackground {
  0% {
    background-size: 40px 40px;
  }

  100% {
    background-size: 40px 37px;
  }
}

@keyframes fadeInTitle {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

